.progress-bar {
  margin: 1em;
  text-align: center;
  position: relative; }

.svg {
  transform: rotate(180deg);
  width: 60%;
  max-width: 200px; }

.mid-text {
  position: absolute;
  height: 100%;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  justify-content: center; }

.signs {
  color: white;
  margin-bottom: 0.75em; }

.signs-number {
  font-weight: 600; }

.goal-desc {
  font-size: 0.875em; }

.unfilled {
  stroke: rgba(255, 255, 255, 0.75);
  fill: transparent; }

.filled {
  stroke: white;
  fill: transparent; }
