.menu-toggler {
  z-index: 910;
  cursor: pointer;
  position: fixed;
  top: 1.1em;
  right: 1.5em; }
  .menu-toggler .bar1, .menu-toggler .bar2, .menu-toggler .bar3 {
    width: 24px;
    height: 2px;
    background-color: white;
    margin: 7px 0;
    transition: 0.2s; }
  .menu-toggler.opened {
    background-color: white; }
    .menu-toggler.opened .bar1, .menu-toggler.opened .bar2, .menu-toggler.opened .bar3 {
      background-color: black; }
    .menu-toggler.opened .bar1 {
      -webkit-transform: rotate(-45deg) translate(-6px, 7px);
      transform: rotate(-45deg) translate(-6px, 7px); }
    .menu-toggler.opened .bar2 {
      opacity: 0; }
    .menu-toggler.opened .bar3 {
      -webkit-transform: rotate(45deg) translate(-6px, -7px);
      transform: rotate(45deg) translate(-6px, -7px); }
